* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

/* Float four columns side by side */

/* Style the counter cards */
.column {
  margin-top: 50px;
  float: left;
  width: 25%;
  margin-left: 80px;
  padding: 0px 10px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); /* this adds the "card" effect */
  padding: 16px;
  text-align: center;
  background-color: #f1f1f1;
  height: 320px;
  border-bottom: 2px rgb(16, 92, 85) solid;
  border-right: 3px rgb(129, 30, 30) solid;
}
/* Remove extra left and right margins, due to padding in columns */
.con1 {
  margin: 0 -5px;
  margin-left: 50px;
  height: 400px;
  /*background-color: green;*/
}
@media screen and (max-width: 600px) {
  .column {
    align-items: center;
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}

/* Clear floats after the columns */
.con:after {
  content: "";
  display: table;
  clear: both;
}

.input-data {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); /* this adds the "card" effect */
  padding: 16px;
  text-align: center;
  background-color: #f1f1f1;
  height: 320px;
  width: 460px;
  margin-left: 550px;
  margin-top: 30px;
}

.input {
  margin-top: 50px;
  width: 210px;
  height: 30px;
}

label {
  padding: 20px;
  font-size: 25px;
}

.button {
  float: right;
  width: 60px;
  height: 30px;
  border-radius: 12px;
}
